import Vue from 'vue'
import Router from 'vue-router'
import appCreditCar from '@/components/appCreditCar'
import creditCarDetails from '@/components/creditCarDetails'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'appCreditCar',
      component: appCreditCar
    },
    {
      path: '/creditCarDetails',
      name: 'creditCarDetails',
      component: creditCarDetails
    }
  ]
})



