<template>
  <div class="container col-md-12 col-sm-12 col-xs-12">
    <el-form
      :model="ruleForm"
      status-icon
      label-width="100px">
      <el-form-item label="姓名：">
        <el-input size="small" v-model="ruleForm.name" placeholder="请输入姓名"></el-input>
      </el-form-item>
      <el-form-item label="电话：">
        <el-input size="small" v-model="ruleForm.phoneNumber" placeholder="请输入电话"></el-input>
      </el-form-item>
      <el-form-item label="公司座机：">
        <el-input size="small" v-model="ruleForm.companyPhone" placeholder="请输入公司座机"></el-input>
      </el-form-item>
      <el-form-item label="公司名称：">
        <el-input size="small" v-model="ruleForm.companyName" placeholder="请输入公司名称"></el-input>
      </el-form-item>
      <el-form-item label="公司地址：">
        <el-input size="small" v-model="ruleForm.companyAddress" placeholder="请输入公司地址"></el-input>
      </el-form-item>
      <el-form-item label="住宅地址：">
        <el-input size="small" v-model="ruleForm.houseAddress" placeholder="请输入住宅地址"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="doSubmit">提交</el-button>
        <el-button size="small" @click="doReset">重置</el-button>
        <el-button size="small" @click="goBackPage">上一步</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  name: 'appCreditCar',
  data () {
    return {
      ruleForm: {
        name: '',
        phoneNumber: '',
        companyName: '',
        companyPhone: '',
        companyAddress: '',
        houseAddress: ''
      }
    }
  },
  methods: {
    doSubmit () {
      console.info('提交！')
    },
    doReset () {
      this.$confirm('确认清空所填内容？')
        .then(_ => {
          this.ruleForm = {
            name: '',
            phoneNumber: '',
            companyName: '',
            companyPhone: '',
            companyAddress: '',
            houseAddress: ''
          }
        })
        .catch(_ => {});
    },
    goBackPage () {
      this.$router.push({ path: '/creditCarDetails' });
    }
  }
}
</script>



